/* Hide on load, show later when toggled */
#sidebar,
#sidebar-overlay {
  display: none;
}
#sidebar {
  position: fixed;
  width: 240px;
  top: 0;
  bottom: 0;
  left: -240px;
  z-index: 1032;
  display: block;
  overflow-x: hidden;
  overflow-y: auto;
  /* Scrollable contents if viewport is shorter than content. */
  background-color: #f5f5f5;
  transition: .3s ease all;
  padding-bottom: 2em;
}
#sidebar-title {
  position: relative;
  padding: 15px;
  background-color: #2196F3;
}
#sidebar-title .redooor-nav-logo {
  font-size: 24px;
  color: #fff;
}
