  height: 28px;
  vertical-align: text-top;
}
#sidebar-overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 1031;
  background: #333333;
  background: rgba(0, 0, 0, 0.4);
  transition: .3s ease all;
}
#sidebar-wrapper.active #sidebar {
  left: 0px;
}
#sidebar-wrapper.active #sidebar-overlay {
  display: block;
}
#sidebar-wrapper.active .main-content {
  height: 100%;
  width: 100%;
  overflow: hidden;
